import React, {useState} from 'react';
import { Modal, Form, Input, Button } from 'antd';
import commonStyles from "../../common.module.scss";


export const LoginModal = () => {
    const [visible, setVisible] = useState(false);

    const showModal = () => {
        setVisible(true);
    };

    const handleCancel = () => {
        setVisible(false);
    };

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
    };
    return (
        <>
            <Button onClick={showModal} className={commonStyles.default_button}>Log In</Button>
            <Modal
                title=""
                visible={visible}
                onCancel={handleCancel}
                footer={null}
            >
                <Form onFinish={onFinish}>
                    <Form.Item
                        label=""
                        name="email"
                        rules={[
                            {
                                required: true,
                                message: ',   !',
                            },
                            {
                                type: 'email',
                                message: ',    !',
                            },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: ',   !',
                            },
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item>
                        <Button className={commonStyles.primary_button} htmlType="submit"></Button>
                    </Form.Item>
                </Form>
            </Modal>
        </>
    )
}