import React, {useState} from 'react';
import { Modal, Form, Input, Select, Button } from 'antd';
import commonStyles from "../../common.module.scss";

const { Option } = Select;

export const RegistrationModal = () => {
    const [visible, setVisible] = useState(false);

    const showModal = () => {
        setVisible(true);
    };

    const handleCancel = () => {
        setVisible(false);
    };

    const onFinish = (values) => {
        console.log('Received values of form: ', values);
    };
    return (
    <>
        <Button type={'primary'} onClick={showModal} className={commonStyles.primary_button}>Sign Up</Button>
        <Modal
            title=""
            visible={visible}
            onCancel={handleCancel}
            footer={null}
        >
            <Form onFinish={onFinish}>
                <Form.Item
                    label=""
                    name="name"
                    rules={[
                        {
                            required: true,
                            message: ',   !',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label=""
                    name="position"
                    rules={[
                        {
                            required: true,
                            message: ',   !',
                        },
                    ]}
                >
                    <Select>
                        <Option value="frontend">Frontend Developer</Option>
                        <Option value="backend">Backend Developer</Option>
                        <Option value="designer">Designer</Option>
                        <Option value="analyst">Analyst</Option>
                    </Select>
                </Form.Item>
                <Form.Item
                    label="  "
                    name="about"
                    rules={[
                        {
                            required: true,
                            message: ',    !',
                        },
                    ]}
                >
                    <Input.TextArea />
                </Form.Item>
                <Form.Item
                    label=""
                    name="email"
                    rules={[
                        {
                            required: true,
                            message: ',   !',
                        },
                        {
                            type: 'email',
                            message: ',    !',
                        },
                    ]}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    label=""
                    name="password"
                    rules={[
                        {
                            required: true,
                            message: ',   !',
                        },
                    ]}
                >
                    <Input.Password />
                </Form.Item>
                <Form.Item>
                    <Button className={commonStyles.primary_button} htmlType="submit"></Button>
                </Form.Item>
            </Form>
        </Modal>
    </>
    )
}